import React from 'react';
import '../css/ImageList.css'
import IntroContainer from './IntroContainer';

class ImageList extends React.Component {
    constructor(props) {
        super(props)
    
        this.state = {
            imageDir: '../images/normal/'
        }
      }
    // create img element
    createImg(src) {
        return new Promise((resolve, reject) => {
            let img =  new Image();
            img.src = src;
            let imgContainer = document.createElement('img');
            imgContainer.setAttribute('data-src', src);
            imgContainer.setAttribute('loading', 'lazy');
            imgContainer.alt = '';
            resolve(imgContainer);
        });
    }
    // pre loading image
    preLoading() {
        const imageIdx = this.props.imageIdx;
        const preImageIdx = imageIdx - 1;
        const nxImageIdx = imageIdx + 1;
        const nowContainer = document.getElementsByClassName(`slide${imageIdx}`)[0];
        this.loadImage(nowContainer, imageIdx);

        if(preImageIdx > 0) {
            const preContainer = document.getElementsByClassName(`slide${preImageIdx}`)[0];
            this.loadImage(preContainer, preImageIdx);
        }

        if(nxImageIdx < this.props.imageList.length) {
            const nxContainer = document.getElementsByClassName(`slide${nxImageIdx}`)[0];
            this.loadImage(nxContainer, nxImageIdx);
        }
    }

    loadImage(container, idx) {
        if(container.classList.contains('image')) return ;
        container.classList.add('image');
        const src = require(`../images/normal/${this.props.imageList[idx][0]}`);
        this.createImg(src).then(imgContainer => {
            container.append(imgContainer);
        });
    }
    // change the displayed image
    async changeDisImage() {
        const preIdx = this.props.preimageIdx;
        const nowIdx = this.props.imageIdx;
        if(preIdx === -1 || preIdx === nowIdx) return ;

        const nowContainer = document.getElementsByClassName(`slide${nowIdx}`)[0];
        const preContainer = document.getElementsByClassName(`slide${preIdx}`)[0];
        preContainer.classList.add('fade');
        await this.wait(600);
        preContainer.classList.remove('fade');
        preContainer.classList.remove('active');
        nowContainer.classList.add('active');
        const nowImg = nowContainer.childNodes[0];
        nowImg.src = nowImg.dataset.src;
    }

    wait(time) {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve();
            }, time);
        });
    }

    // if menu is open
    changeClassMenuOpen() {
        const nowIdx = this.props.imageIdx;
        const nowImage = document.getElementsByClassName(`slide${nowIdx}`)[0];
        if(this.props.openMenu) {
            nowImage.classList.add('menu-active');
            nowImage.classList.remove('menu-close');
        }else {
            nowImage.classList.add('menu-close');
            nowImage.classList.remove('menu-active');
        }
    }
    
    componentDidUpdate() {
        this.preLoading();
        this.changeDisImage();
        this.changeClassMenuOpen();
    }
    
    render() {
        return (
            <ul id='image-item-container'>
                <li className='slide0 active image' id='first-image-container'>
                    <img id='first-image' data-src={require('../images/normal/main.jpg')} src={require('../images/normal/main.jpg')} alt=''/>
                    <IntroContainer/>
                </li>
                {this.props.imageList.map((image, idx) => (<li key={`slide${idx + 1}`} className={`slide${idx + 1}`}></li>))}
            </ul>
        );
    }
}

export default ImageList;