import React from 'react';
import '../css/secret.css'

class Secret extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            time1: "21:00 BST 03/09 2022",
            time2: "00:02 BST 04/09 2022",
            time3: "03:23 BST 05/09 2022", 
            contents: [
                "",
                " ",
                "5",
                "2",
                "1",
                "",
                " ",
                "241920"
            ]
        }
    }

    setContent() {
        const ulContainer = document.getElementById('main-secret-content');
        this.state.contents.forEach(content => {
            const liContainer = document.createElement('li');
            liContainer.appendChild(document.createTextNode(content));
            ulContainer.appendChild(liContainer);
        })
    }

    componentDidMount() {
        this.setContent();
      }
    render(){
        return (
            <div id='secret'>
                <div id='secret-content'>
                    <div id='time1' className='time'>{this.state.time1}</div>
                    <ul id='main-secret-content'></ul>
                    <div id='time2' className='time'>{this.state.time2}</div>
                    <div id='comment'>[]</div>
                    <div id='time3' className='time'>{this.state.time3}</div>
                    <div id='extra'>The inherent reason is: You are the optimal solution for my life equation from both emotional and realistic perspectives and I beg you can give me a chance to meet you in October.</div>
                </div>
            </div>
        );
    }
}

export default Secret;