import React from 'react';
// import IntroContainer from './components/IntroContainer';
import Gallery from './components/Gallery';
import Menu from './components/Menu';
// import Profile from './components/Profile';
import IMAGES from './images.json';

class App extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      // if is the first image shown, the first image is the image that includes myself
      isFirstImage: true,
      // the image idx in the image list
      imageIdx: 0,
      // previous image idx
      preimageIdx: -1,
      // image lists
      imageList: [['main.jpg', {'addr': 'QIYAO YIN'}]],
      // how many images for each addr
      imageAddrDic: [['main', 1]],
      // imageDesList: ,
      // if open profile module
      openProfile: false,
      // if open menu module
      openMenu: false
    }
  }
  // set ImageList
  setImageList() {
    let list = this.state.imageList
    let dic = this.state.imageAddrDic
    // let des = this.state.imageDesList;
    Object.keys(IMAGES).forEach(key => {
      for(const image of IMAGES[key]) {
        list.push([key + '/' + image, {'addr': key}]);
      }
      dic.push([key, IMAGES[key].length]);
      console.log(dic);
    });
    this.setState({
      imageList: list,
      imageAddrDic: dic
    });
  }

  // set imageIdx
  setImageIdx(addition) {
    const preIdx = this.state.imageIdx;
    let nextIdx = this.state.imageIdx + addition;
    nextIdx = nextIdx < 0 ? 0 : nextIdx;
    nextIdx = nextIdx >= this.state.imageList.length ? this.state.imageList.length - 1 : nextIdx;
    this.setState({
      imageIdx: nextIdx,
      preimageIdx: preIdx
    });
  }

  changeImageIdx(idx) {
    const preIdx = this.state.imageIdx;
    this.setState({
      imageIdx: idx,
      preimageIdx: preIdx
    });
  }

  // set profile
  // setProfile() {
  //   const nxProfileState = !this.state.openProfile;
  //   this.setState({
  //     openProfile: nxProfileState,
  //   });
  // }
  
  // scroll the position of tiny image
  scrollTinyImage() {
    const nowIdx = this.state.imageIdx;
    const ulContainer = document.getElementById('menu-images-container');
    const imagePosition = (nowIdx - 1) * 96;
    ulContainer.scrollTo(imagePosition, 0);
}
  // change active image 
  changeActImage() {
    const nowIdx = this.state.imageIdx;
    const preIdx = this.state.preimageIdx;
    if(preIdx >= 0) document.getElementsByClassName(`tiny${preIdx}`)[0].classList.remove('active');
    if(nowIdx >= 0) document.getElementsByClassName(`tiny${nowIdx}`)[0].classList.add('active');
  }

  // change menu open or not
  setOpenMenu() {
    const nowOpenMenu = !this.state.openMenu;
    this.setState({
      openMenu: nowOpenMenu,
    });
  }

  componentDidMount() {
    this.setImageList();
  }

  componentDidUpdate() {
    this.changeActImage();
  }
  render() {
    return (
      <div id='app'>
        <Menu
          imageList = {this.state.imageList}
          imageIdx = {this.state.imageIdx}
          preimageIdx = {this.state.preimageIdx}
          changeImageIdx = {imageIdx => this.changeImageIdx(imageIdx)}
          imageAddrDic = {this.state.imageAddrDic}
          setOpenMenu = {() => this.setOpenMenu()}
        />
        {/* <Profile
          openProfile = {this.state.openProfile}
          setProfile = {() => this.setProfile()}
        /> */}
        {/* <IntroContainer
          imageIdx = {this.state.imageIdx}
          // setProfile = {() => this.setProfile()}
        /> */}
        <Gallery
          imageList = {this.state.imageList}
          imageIdx = {this.state.imageIdx}
          preimageIdx = {this.state.preimageIdx}
          setImageIdx = {addition => this.setImageIdx(addition)}
          scrollTinyImage = {() => this.scrollTinyImage()}
          openMenu = {this.state.openMenu}
        />
      </div>
    );
  }
}

export default App;
