import React from 'react';
import '../css/date.css'
import CryptoJS from 'crypto-js';

class DateCV extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            key: "qiyaoyin",
            keyHex: CryptoJS.enc.Utf8.parse(this.key),
            selfInformation: {
                name: "b0c180253f314eaadaa2e0214828ce4e",
                birth: "42635b0a8761451cd219a2d298fd8a1f",
                birthPlace: "a5e7a19aefa7a597d60b808e94c10d5a",
                height: "175CM",
                weight: "55KG",
                media: {
                    email: '47ef8043a921ac4457dff10213e675ae05dbe06c4fc25d0a',
                    wechat: '8e48f002eb82eebed02cf4d682bb09d3618c6f8cb69097e7'
                },
            },
            workInformation: {
                first: "9843fda086c0f8d6899dbcb1d5dac9a2715fe13f3b03eb49178b72aec83c4e25a0c9d4fa262504cad035e693965f41bad21cba64559383cabc9f99e5cca857e35ae49aad23c904afeb37592d76f592040ce3450dc72bd35fe4a652deb0f44cfe",
                salary: "79c1209b36bbaa01",
            },
            educationInformation: {
                undergraduate: "8a282082549ce0c5798b65c9a53f4730a4d170ae4375917434fcbab4ab6fb6db7bf50a85e8783b2f740eac7a481204ae139a7a4a52e5bc87073a4285459daa4e",
                postgraduate: "4bf194162c36ada5558c26308a5d21148af37a6beb56bfeff97b9e752919ed1491a77feb5b22972d3b7fe198330726c96072bcb62ea86f74c2b7fb6f46ecfd09f306add95e66b673",
            },
            hobbyInformation: "",
            requireInformation: "316554",
            pics: ['../images/me/me1.jpg','../images/me/me2.jpg','../images/me/me3.jpg']
        }
    }
    // encrypto
    encryptDES(message) {
        if (message) {
            let encrypted = CryptoJS.DES.encrypt(message, this.state.keyHex, {
                mode: CryptoJS.mode.ECB,
                padding: CryptoJS.pad.Pkcs7
            });
            return encrypted.ciphertext.toString();
        } else {
            return '';
        }
    }
  
    // decrypto
    decryptDES(ciphertext) {
        if (ciphertext) {
            let decrypted = CryptoJS.DES.decrypt({
                ciphertext: CryptoJS.enc.Hex.parse(ciphertext)
            }, this.state.keyHex, {
                mode: CryptoJS.mode.ECB,
                padding: CryptoJS.pad.Pkcs7
            });
            return decrypted.toString(CryptoJS.enc.Utf8);
        } else {
            return '';
        }
    }

    //touch event
    onClick(e) {
        e.preventDefault();
        const picDes = document.getElementById("pics-description");
        if(picDes.className === 'hidden') {
            picDes.classList.remove('hidden');
            console.log('hidden');
        }
    }

    componentDidMount() {
       // console.log(this.encryptDES(this.state.selfInformation.media.wechat))
    }

    render(){
        return (
            <div id='cv-container'>
                <div id='self-information-container'>
                    <div id='basic-information-container'>
                        <div className='information-container' id='my-one-inch-photo'>
                            <img alt='' src={require('../images/normal/me.png')} data-src={require('../images/normal/me.png')}/>
                        </div>
                        <ul id='self-information-list'>
                            <li className='self-information-item' id='name-item'>{this.decryptDES(this.state.selfInformation.name)} </li>
                            <li className='self-information-item'>{this.state.selfInformation.height} <p className='self-information-break'></p> {this.state.selfInformation.weight} <p className='self-information-break'></p> {this.decryptDES(this.state.selfInformation.birth)} <p className='self-information-break'></p> {this.decryptDES(this.state.selfInformation.birthPlace)}</li>
                            <li className='self-information-item'>{this.decryptDES(this.state.selfInformation.media.wechat)} <p className='self-information-break'></p> {this.decryptDES(this.state.selfInformation.media.email)}</li>
                        </ul>
                    </div>
                    <div className='information-container' id='education-information-container'>
                        <div className='information-title' id='education-information-title'></div>
                        <div className='information-description' id='work-description'>
                            <div>{this.decryptDES(this.state.educationInformation.undergraduate)}</div>
                            <div>{this.decryptDES(this.state.educationInformation.postgraduate)}</div>
                        </div>
                    </div>
                    <div className='information-container' id='work-information-container'>
                        <div className='information-title' id='work-information-title'></div>
                        
                        <div className='information-description' id='work-description'>
                            {this.decryptDES(this.state.workInformation.first) + this.decryptDES(this.state.workInformation.salary)}
                        </div>
                    </div>
                    <div className='information-container' id='hobby-information-container'>
                        <div className='information-title' id='hobby-information-title'></div>
                        <div className='information-description' id='hobby-description'>
                            {this.state.hobbyInformation}
                        </div>
                    </div>
                    {/* <div className='information-container' id='require-information-container'>
                        <div className='information-title' id='require-information-title'></div>
                        <div className='information-description' id='require-description'>
                            {this.state.requireInformation}
                        </div>
                    </div> */}
                    <div className='information-container' id='pics-information-container'>
                        <div className='information-title' id='pics-information-title'
                        onClick = {(e) => this.onClick(e)}
                        ></div>
                        <div className='information-description' id='pics-description'>
                            <ul id='pics-list'>
                                <li className='pics-item'><img alt='1' data-src={require('../images/me/me1.jpg')} src={require('../images/me/me1.jpg')}/></li>
                                {/* <li className='pics-item'><img alt='2' data-src={require('../images/me/me2.jpg')} src={require('../images/me/me2.jpg')}/></li> */}
                                <li className='pics-item'><img alt='3' data-src={require('../images/me/me3.jpg')} src={require('../images/me/me3.jpg')}/></li>
                            </ul>
                        </div>
                    </div>
                </div>

                {/* <div id='pics-description' className='hidden'>
                    <ul id='pics-list'>
                        <li className='pics-item'><img alt='pic1' data-src={require('../images/me/me1.jpg')} src={require('../images/me/me1.jpg')}/></li>
                        <li className='pics-item'><img alt='pic2' data-src={require('../images/me/me2.jpg')} src={require('../images/me/me2.jpg')}/></li>
                        <li className='pics-item'><img alt='pic3' data-src={require('../images/me/me3.jpg')} src={require('../images/me/me3.jpg')}/></li>
                    </ul>
                </div> */}
            </div>
        );
    }
}

export default DateCV;