import React from 'react';
import ImageList from './ImageList';
import GalleryArray from './GalleryArrow';
import ImageDescription from './ImageDescription';

class Gallery extends React.Component {
    render() {
        return (
            <div id='gallery-container'>
                <GalleryArray
                    setImageIdx = {addition => this.props.setImageIdx(addition)}
                    scrollTinyImage = {() => this.props.scrollTinyImage()}
                    imageIdx = {this.props.imageIdx}
                />
                <ImageList
                    imageList = {this.props.imageList}
                    imageIdx = {this.props.imageIdx}
                    preimageIdx = {this.props.preimageIdx}
                    openMenu = {this.props.openMenu}
                />
                <ImageDescription
                    imageList = {this.props.imageList}
                    imageIdx = {this.props.imageIdx}
                />
            </div>
        );
    }
}

export default Gallery;