import React from 'react';
import '../css/ImageDescription.css'
import { isMobile } from "react-device-detect";

class ImageDescription extends React.Component {
    // mouse enter to the description region, show details
    MouseEnter = e => {
        if(e.currentTarget === e.target) {
            // e.currentTarget.className = "show";
            document.getElementById("image-description").className = 'show';
        }
    }
    // mouse leave the description region, hide details
    MouseLeave = e => {
        if(e.currentTarget === e.target) {
            // e.currentTarget.className = "hide";
            document.getElementById("image-description").className = 'hide';
        }
    }

    // touch event same as mouseenter and mouseleave
    TouchEnd = e => {
        e.preventDefault();
        const className = e.currentTarget.classList.value;
        if(className === "hide") {
            this.MouseEnter(e);
        }else if(className === "show") {
            this.MouseLeave(e);
        }
    }

    // set description
    setDescription() {
        if(this.props.imageIdx === 0) {
            document.getElementById("image-description-container").className = "hide";
        }else {
            document.getElementById("image-description-container").className = "show";
        }
        const imageDes = this.props.imageList[this.props.imageIdx][1];
        const container = document.getElementById('image-description');
        container.innerHTML = '';
        Object.keys(imageDes).forEach(key => {
            // console.log(key + imageDes[key]);
            let list = document.createElement('li');
            list.classList.add('image-description');
            list.classList.add(key);
            list.textContent = imageDes[key];
            container.append(list);
        });
    }

    componentDidUpdate() {
        this.setDescription();
    }
    render() {
        return (
            <div id='image-description-container'
                className='hide'
                onMouseEnter = {isMobile ? () => (false) : this.MouseEnter}
                onMouseLeave = {isMobile ? () => (false) : this.MouseLeave}
                onTouchEnd = {this.TouchEnd}
            >
                <ul id='image-description'
                    className='hide'
                >
                    {/* <li className='image-description'>sony-6500</li>
                    <li className='image-description'>83mm</li>
                    <li className='image-description'>f/5.6</li>
                    <li className='image-description'>1/200</li> */}
                </ul>
            </div>
        );
    }
}

export default ImageDescription;