import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import Secret from './routes/Secret';
import DateCV from './routes/DateCV';

import {
    BrowserRouter,
    Routes,
    Route,
  } from "react-router-dom";

const root = ReactDOM.createRoot(document.getElementById('root'));
// root.render(<App />);
root.render(
  <BrowserRouter>
    <Routes>
      <Route path="/" element={<App />} />
      <Route path="secretRoute" element={<Secret />} />
      <Route path='DateCV' element={<DateCV/>}/>
    </Routes>
  </BrowserRouter>
);