import React from 'react';
import '../css/Menu.css';
import 'lazysizes'

class Menu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isMountedGroup: false,
        }
    }
    // click menu icon close menu or open menu
    onClickMenuIcon = e => {
        const className = e.currentTarget.classList.value;
        if(className === 'close') {
            e.currentTarget.className = "open";
            document.getElementById("menu-container").className = 'open';
            document.getElementById("menu-detail-container").className = 'open';
        }else if(className === 'open') {
            e.currentTarget.className = "close";
            document.getElementById("menu-container").className = 'close';
            document.getElementById("menu-detail-container").className = 'close';
        }
        this.props.setOpenMenu();
    }

    // on click tiny image
    onClickTinyImage = e => {
        e.preventDefault();
        this.props.changeImageIdx(parseInt(e.currentTarget.getAttribute('idx')));
    }
    // hover tiny image
    onMouseEnterTinyImage = e => {
        e.currentTarget.classList.add('hover');
    }
    onMouseLeaveTinyImage = e => {
        e.currentTarget.classList.remove('hover');
    }

    // group tiny image by address
    groupTinyImage() {
        if(this.state.isMountedGroup) return ;
        const imageAddrDic = this.props.imageAddrDic;
        let prelen = imageAddrDic[0][1];
        for(let i = 1; i < imageAddrDic.length; ++i) {
            const listContainer = document.getElementsByClassName(`tiny${prelen}`)[0];
            const notationContainer = document.createElement('li');
            notationContainer.classList.add('addr-notation-container');
            document.getElementById('menu-images-container').insertBefore(notationContainer, listContainer);
            prelen += imageAddrDic[i][1];
        }
        this.setState({
            isMountedGroup: true
        });
    }
    
    componentDidUpdate() {
        this.groupTinyImage();
    }

    render(){
        return(
            <div id='menu-container'
                className='close'
            >
                <div id='menu-detail-container'
                    className='close'
                >
                    <ul id='menu-images-container'>
                    {
                        this.props.imageList.map((image, idx) => (
                            <li key={`tiny${idx}`} className={`tiny-image-list tiny${idx}`}>
                            <img className = 'tiny-image lazyload' idx = {idx} data-src = {require(`../images/tiny/${image[0]}`)} alt = {image[1]['addr']} width='80px' height='80px' 
                            onClick = {this.onClickTinyImage}
                            onMouseEnter = {this.onMouseEnterTinyImage}
                            onMouseLeave = {this.onMouseLeaveTinyImage}
                            />
                            <div className={`tiny-iamge-addr-annotation annotation${idx} deactive`}>
                                {image[1]['addr']}
                            </div>
                            </li>
                        ))
                    }
                    </ul>
                    {/* <div id='about-container'
                    onClick={this.onClickAboutMe}>
                        About Me
                    </div> */}
                </div>
                <div id='menu-icon'
                className='close'
                onClick = {this.onClickMenuIcon}
                ></div>
            </div>
        );
    }
}

export default Menu;